import util from '../../utils/index.js'
import uniIcons from "@/components/uni-icons/uni-icons.vue"
import uniBadge from "@/components/uni-badge/uni-badge.vue"
export default {
	components: {
		"uni-icons":uniIcons,
		"uni-badge":uniBadge
	},
	props: {
		repairitem: {
			type: Object
		}
	},
	computed: {
		imglist() {
			const that = this;
			return this.repairitem.images.map(function(i){
				return that.repairimgurl+i.filename;
			}) 
		},
		isdeal(){
			if(this.repairitem.status === '01' && this.repairitem.dealuserid>0){
				return true;
			}
		},
		issend(){
				if(this.repairitem.status === '01' && this.repairitem.senduserid>0){
					return true;
				}
		},
		ischeck(){
				return false;
		}
	},
	mounted() {
		this.inituserinfo();
	},
	data() {
		return {
			userinfo:{},
			repairimgurl: util.rooturl + 'repair/'
		}
	},
	methods: {
		inituserinfo(){
			const userinfo = uni.getStorageSync('userinfo');
			if(userinfo){
				this.userinfo = userinfo;
			}
		},
		previewimgs(imagepath) {
			uni.previewImage({
				urls: this.imglist,
				current: imagepath,
				indicator: 'default',
				longPressActions: {
					itemList: ['发送给朋友', '保存图片', '收藏'],
					success: function(data) {
						console.log(data.index + 1);
					},
					fail: function(err) {
						console.log(err.errMsg);
					}
				},
				success(res) {
					console.log(res);
				}
			})
		},
		deal_repairbill(){
			this.$emit('dealrepair',this.repairitem);
		},
		send_repairbill(){
			this.$emit('sendrepair',this.repairitem);
		},
		check_repairbill(){
			this.$emit('checkrepair',this.repairitem);
		},
		view_deal_detail()
		{
			this.$emit('repairdetail',this.repairitem);
		}
	},
}
